.fa-times-thin:before {
  content: '\D7'; }

select {
  -webkit-appearance: none;
  -moz-appearance: none; }

select::-ms-expand {
  display: none; }

*:active,
*:focus {
  outline: none;
  outline-style: none; }

*::-moz-focus-inner {
  outline: none;
  border: 0; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #ffffff inset; }

html {
  height: 100%;
  width: 100%; }

body {
  position: relative;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #111111;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  height: 100%; }

@font-face {
  font-family: "Open Sans";
  src: url(/public/src/fonts/open-sans/OpenSans-Regular.ttf); }

.ui-button {
  width: auto;
  height: 30px;
  padding: 0px 28px;
  margin: 6px;
  line-height: 30px;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  color: #111111;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 1px solid #ffffff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.49);
  border-radius: 4px;
  overflow: hidden;
  float: left;
  display: block;
  text-decoration: none;
  text-align: center; }
  .ui-button:hover {
    cursor: pointer;
    text-decoration: none; }
  .ui-button:active {
    cursor: pointer;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.49);
    text-decoration: none; }

.ui-button_circle_s1 {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  padding: 0; }

.ui-button_circle_s2 {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 0;
  font-size: 18px;
  line-height: 41px;
  border-width: 2px !important; }

.ui-button_circle_s3 {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding: 0;
  font-size: 23px;
  line-height: 48px;
  border-width: 2px !important; }

.ui-button_white {
  color: #111111;
  border: 1px solid #ffffff;
  background-color: #ffffff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.49); }
  .ui-button_white:hover {
    cursor: pointer;
    text-decoration: none;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.49); }
  .ui-button_white:active {
    cursor: pointer;
    box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.49);
    text-decoration: none; }

.ui-button_black {
  color: #ffffff;
  border: 1px solid #111111;
  background-color: #111111;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.49); }
  .ui-button_black:hover {
    cursor: pointer;
    text-decoration: none;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.49); }
  .ui-button_black:active {
    cursor: pointer;
    box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.49);
    text-decoration: none; }

.ui-button_float_white {
  color: #ffffff;
  border: 1px solid #ffffff;
  background-color: #00000000;
  box-shadow: none; }
  .ui-button_float_white:hover {
    background-color: rgba(255, 255, 255, 0.2);
    box-shadow: none; }
  .ui-button_float_white:active {
    background-color: rgba(255, 255, 255, 0.4);
    box-shadow: none; }

.ui-button_float_black {
  color: #111111;
  border: 1px solid #111111;
  background-color: #00000000;
  box-shadow: none; }
  .ui-button_float_black:hover {
    background-color: rgba(17, 17, 17, 0.2);
    box-shadow: none; }
  .ui-button_float_black:active {
    background-color: rgba(17, 17, 17, 0.4);
    box-shadow: none; }

.ui-button_noborder {
  border: none; }
