.fa-times-thin:before {
  content: '\D7'; }

select {
  -webkit-appearance: none;
  -moz-appearance: none; }

select::-ms-expand {
  display: none; }

*:active,
*:focus {
  outline: none;
  outline-style: none; }

*::-moz-focus-inner {
  outline: none;
  border: 0; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #ffffff inset; }

html {
  height: 100%;
  width: 100%; }

body {
  position: relative;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #111111;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  height: 100%; }

.wg-card-user {
  position: relative;
  width: 264px;
  height: 278px;
  background: #ffffff;
  box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.49);
  margin: 15px; }
  .wg-card-user__button-close {
    position: absolute;
    right: 0; }
  .wg-card-user__button-menu {
    position: absolute;
    right: 0; }
  .wg-card-user__photo {
    height: 210px;
    width: 100%;
    background-color: #00000061;
    overflow: hidden; }
  .wg-card-user__img {
    height: 100%;
    width: auto; }
  .wg-card-user__nickname {
    color: #111111;
    font-size: 16px;
    font-weight: bold;
    padding: 10px 22px 0px 22px;
    display: block; }
  .wg-card-user__name {
    color: #111111;
    font-size: 14px;
    padding: 6px 22px 0px 22px;
    display: block; }

.wg-form-registration {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  background: #1111116e;
  z-index: 200;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end; }
  .wg-form-registration__container {
    width: 100%;
    height: auto;
    display: block; }
  .wg-form-registration__card {
    position: relative;
    max-width: 900px;
    min-width: 500px;
    height: auto;
    background: #ffffff;
    box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.49);
    margin-bottom: 50px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    padding-bottom: 35px; }
  .wg-form-registration__card-header {
    color: #111111;
    font-size: 24px;
    font-weight: 400;
    margin: 35px 45px; }
  .wg-form-registration__card-buttons {
    color: #111111;
    font-size: 24px;
    font-weight: 400;
    margin: 35px 40px; }

.wg-form-registration__menu {
  position: relative;
  top: 0;
  right: 0;
  background: #00000000;
  height: 66px;
  max-width: 900px;
  min-width: 500px;
  z-index: 100;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .wg-form-registration__menu-header {
    color: #ffffff;
    font-size: 30px;
    font-weight: 400;
    margin: 50px 0px 0px 0px; }
  .wg-form-registration__menu-buttons {
    position: absolute;
    right: 0;
    margin-top: -35px; }
