.fa-times-thin:before {
  content: '\D7'; }

select {
  -webkit-appearance: none;
  -moz-appearance: none; }

select::-ms-expand {
  display: none; }

*:active,
*:focus {
  outline: none;
  outline-style: none; }

*::-moz-focus-inner {
  outline: none;
  border: 0; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #ffffff inset; }

html {
  height: 100%;
  width: 100%; }

body {
  position: relative;
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #111111;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
  height: 100%; }

.lt-main {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  position: absolute;
  height: 100%;
  width: 100%;
  background: linear-gradient(to top left, #6e203a 20%, #303f92 50%, #520c72 80%); }

.lt-main__menu {
  height: 100%;
  width: 240px;
  background-color: #2b3340;
  box-shadow: 0px 2px 24px rgba(0, 0, 0, 0.49); }
  .lt-main__menu-profile {
    height: auto;
    width: 100%;
    border-bottom: 3px solid #ffffff;
    text-align: center;
    margin-top: 25px; }
  .lt-main__menu-avatar {
    color: #ffffff;
    font-size: 42px; }
  .lt-main__menu-nickname {
    color: #ffffff;
    font-size: 19px;
    margin-top: 18px;
    font-weight: bold; }
  .lt-main__menu-buttons {
    margin: 20px 0 20px;
    overflow: hidden;
    flex-wrap: wrap;
    display: flex;
    justify-content: center; }
  .lt-main__menu-list {
    list-style: none;
    margin: 20px 0 19px;
    padding-left: 0;
    color: #ffffff; }
  .lt-main__menu-item {
    padding: 8px 20px; }
    .lt-main__menu-item:hover {
      background-color: rgba(255, 255, 255, 0.2);
      box-shadow: none;
      cursor: pointer; }
    .lt-main__menu-item:active {
      background-color: rgba(255, 255, 255, 0.4);
      box-shadow: none;
      cursor: pointer; }

.lt-main__content {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: rgba(48, 48, 48, 0.719);
  height: 100%;
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
  position: relative; }

.lt-main-menu {
  position: absolute;
  top: 0;
  right: 0;
  background: #00000000;
  height: 84px;
  width: 100%;
  z-index: 100; }
  .lt-main-menu__margin {
    height: 84px;
    width: 100%;
    position: relative; }
  .lt-main-menu__header {
    color: #ffffff;
    font-size: 36px;
    font-weight: 400;
    margin: 25px 0px 0px 65px; }
  .lt-main-menu__buttons {
    position: absolute;
    right: 58px;
    margin-top: -35px; }
